import React from "react";
import { photos } from "./Photos";

function PhotoGallery() {

    const fs = require("fs")
    const files = fs.readdir("./assets/Products")
    const photoList = photos.map((photo) => <img key={photo.title} alt="Ürünlerimiz" src={photo.src}/> )
    console.log(files)
    return (

        <div className={"mb6"}>
            {photoList}
        </div>
    );
}


export default PhotoGallery;